'use client';

import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';
import { SimpleSelect } from '@/src/components/ui/select';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';
import { importSubscriptionPackagesFromExcel } from '@/src/lib/subscriptionPackages/importSubscriptionPackages';

interface Props {
  packages: SubscriptionPackage[];
  searchTerm: string;
  setSearchTerm: (term: string) => void;
  durationFilter: string;
  setDurationFilter: (value: string) => void;
  onExport: () => void;
  onImportComplete: (imported: Omit<SubscriptionPackage, 'id' | 'createdAt' | 'updatedAt'>[]) => void;
  onAddNew: () => void;
}

export default function SubscriptionPackageSearchImportExport({
  packages,
  searchTerm,
  setSearchTerm,
  durationFilter,
  setDurationFilter,
  onExport,
  onImportComplete,
  onAddNew,
}: Props) {
  const handleImport = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    try {
      const importedPackages = await importSubscriptionPackagesFromExcel(file, packages[0]?.companyId ?? '');
      onImportComplete(importedPackages);
    } catch (error) {
      console.error('‚ùå Import error:', error);
      alert('Failed to import file.');
    }
  };

  return (
    <div className="flex flex-col md:flex-row md:items-center justify-between gap-4 flex-wrap mb-4">
      {/* Left: Search + Filter */}
      <div className="flex flex-col sm:flex-row gap-2 w-full md:w-auto">
        <Input
          type="text"
          placeholder="Search by package name..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full sm:w-64"
        />

        <SimpleSelect
          value={durationFilter}
          onChange={(val) => setDurationFilter(val)}
          options={[
            { label: 'All', value: '' },
            { label: 'Daily', value: 'daily' },
            { label: 'Monthly', value: 'monthly' },
          ]}
          className="w-full sm:w-40"
        />
      </div>

      {/* Right: Buttons */}
      <div className="flex gap-2 flex-wrap justify-end">
        <Button onClick={onAddNew}>Add New Package</Button>
        <Button onClick={onExport}>Export to Excel</Button>

        <input
          type="file"
          id="import-excel"
          accept=".xlsx"
          className="hidden"
          onChange={handleImport}
        />
        <label htmlFor="import-excel">
          <Button type="button" variant="outline">Import Excel</Button>
        </label>
      </div>
    </div>
  );
}
