import * as XLSX from 'xlsx';
import { SubscriptionPackage } from './subscriptionPackagesType';

export async function importSubscriptionPackagesFromExcel(file: File, companyId: string) {
  return new Promise<Omit<SubscriptionPackage, 'id' | 'createdAt' | 'updatedAt'>[]>((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = (e) => {
      const data = new Uint8Array(e.target?.result as ArrayBuffer);
      const workbook = XLSX.read(data, { type: 'array' });
      const sheet = workbook.Sheets[workbook.SheetNames[0]];
      const jsonData = XLSX.utils.sheet_to_json<any>(sheet);

      const packages = jsonData.map((row) => ({
        companyId,
        name: row['Package Name'] || '',
        durationType: row['Duration Type'] === 'monthly' ? 'monthly' : 'daily',
        kmLimit: row['KM Limit'] === 'Unlimited' ? null : parseInt(row['KM Limit']),
        chargingMethod: row['Charging Method'] === 'self' ? 'self' : 'swap',
        basePrice: parseInt(row['Base Price (VND)']) || 0,
        overageRate: row['Overage Rate (VND/km)'] === '-' ? null : parseInt(row['Overage Rate (VND/km)']),
        note: '',
      }));

      resolve(packages);
    };
    reader.onerror = (err) => reject(err);
    reader.readAsArrayBuffer(file);
  });
}
